html {
    box-sizing: border-box;
    font-size: 62.5%;
}


*,
*:before,
*:after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}


html,
body {
    height: 100%;

}

body::-webkit-scrollbar {
    display: none;
}

body {
    font-size: 1.6rem;
    /* font-size 1.6rem is now the same as 16px */
    line-height: 1.6;
    background-color: #EFEFEF;
}




@font-face {
    font-family: "Overused Grotesk";
    src: url("../typefaces/OverusedGrotesk-VF.woff2") format("woff2-variations");
    font-weight: 800;
    font-stretch: 100%;
    font-style: oblique -10deg;
}



header {
    display: flex;
    flex-direction: row-reverse;
    position: sticky;
    top: 0;
    z-index: 4;
    background-color: #D3D3D3;
    border-bottom-style: solid;
    border-width: .1rem;
    padding: 5px;
    /* width: 10%; */
}

.paddedCon {
    padding: 10px;

}


.body-box {
    padding-top: 30px;


}

h1 {
    font-family: "Overused Grotesk", sans-serif;
    line-height: 10rem;
    font-size: 10.667rem;
    letter-spacing: -.02rem;

}

.title-type h1 {
    max-width: 67vw;
}

h2 {
    font-family: "ibm-plex-serif", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 2.75rem;
    letter-spacing: -.09rem;
    line-height: 3rem;
    max-width: 85vw;

}

h3 {
    font-family: "inter-variable", sans-serif;
    font-size: 2.5rem;

    font-variation-settings:
        "wght" 500,
        "slnt" 0,
        "wdth" 80;

    letter-spacing: -0.09em;
    display: inline-block;
    transform: scaleX(0.75);
    transform-origin: left;
    margin: 0;
}

a {
    text-decoration: none;
    color: black;
}



.handle {
    background-color: white;
    border-style: solid;
    border-width: .1rem;
    border-radius: 150rem;
    width: 160px;
    height: 27.5px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 45px;
    margin-right: 2.5px;
}

.ig {
    background-color: white;
    border-style: solid;
    border-width: .1rem;
    border-radius: 250rem;
    width: 30px;
    height: 27.5px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 4px;
    margin-right: 2.5px;

}

.archive {
    background-color: white;
    border-style: solid;
    border-width: .1rem;
    border-radius: 250rem;
    width: 90px;
    height: 27.5px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 20px;
    margin-top: 5px;
    margin-right: 2.5px;
}

.rules {
    background-color: white;
    border-style: solid;
    border-width: .1rem;
    border-radius: 250rem;
    width: 75px;
    height: 27.5px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 15px;
    margin-top: 5px;
    margin-right: 2.5px;
}

.home {
    background-color: white;
    border-style: solid;
    border-width: .1rem;
    border-radius: 250rem;
    width: 70px;
    height: 27.5px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 15px;
    margin-top: 5px;
    margin-right: 2.5px;
}


.baddesignclub-header {
    /* background-color: white;
    border-style: solid;
    border-width: .2rem;
    border-radius: 150rem; */
    width: 160px;
    height: 27.5px;
    position: fixed;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    /* padding-left: 45px; */
