footer {
    display: flex;
    flex-direction: row;
    position: fixed;
    padding-right: 10px;
    right: 5px;
    bottom: 5vh;
    width: 100%;
    justify-content: flex-end;
    z-index: 6;
}



.rule-box {
    display: flex;
    height: 80vh;
    align-items: center;
}

.rule-box h2 {
    font-family: "ibm-plex-serif", sans-serif;
    font-weight: 300;
    font-style: normal;
    line-height: 55.5pt;
}


.body-image {
    margin-top: 35px;

}

.body-image img {
    /* width: 400px; */

    max-width: 25vw;
    animation-name: imageAnimation;
    animation-duration: 1.5s;
    filter: grayscale(1);
}

@keyframes imageAnimation {
    0% {
        filter: none;
    }

    100% {
        filter: grayscale(1);

    }
}

.archive-box {
    margin: -30px 0 -30px 0;

}

.archive-box h2 {
    position: relative;
    z-index: -5;
    font-family: "ibm-plex-serif", sans-serif;
    font-weight: 300;
    font-style: normal;
    line-height: 55.5pt;
}

.archive-image {
    margin-top: 15px;
    padding-top: 105px;
}

.archive-image img {
    width: 30%;
}


@media (min-width: 640px) {

    .body-box h2 {
        max-width: 30vw;
    }

    /* 
    h1 {
        max-width: 35vw;
    } */

}

@media (min-width: 500px) and (max-width: 640px) {

    .body-box h2 {
        max-width: 50vw;
    }

    h1 {
        max-width: 55vw;
    }


}

@media (max-width: 499px) {
    .title-type h1 {
        font-size: 8.2rem;
        line-height: 7.5rem;
        max-width: 95vw;
    }

    .body-image img {
        max-width: 75vw;
    }
}